export default {
  components: {
    XInput,
    XButton,
    Box,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    picker () {
      let self = this
      let wx = this.$wechat
      if (self.serverIds.length >= 6) {
        self.alert('6')
        return
      }
      wx.chooseImage({
        count: 1, // 9
        sizeType: ['original', 'compressed'], // 
        sourceType: ['camera'], // 
        success (res) {
          // res.localIds[0] // IDlocalIdimgsrc
          const localId = res.localIds[0]
          // 
          if (navigator.userAgent.toLocaleLowerCase().indexOf('iphone') !== -1) {
            wx.getLocalImgData({
              localId: localId, // localID
              success (res) {
                let localData = res.localData // localDatabase64img
                localData = localData.replace('jgp', 'jpeg')// iOS  image/jgp,
                self.localImages.push(localData)
              },
              fail (res) {
                self.alert('')
              }
            })
          } else {
            self.localImages.push(localId)
          }
          // 
          wx.uploadImage({
            localId: localId, // IDchooseImage
            isShowProgressTips: 1, // 1
            success (res) {
              // resolve(res.serverId) // ID
              self.serverIds.push(res.serverId)
              self.serverId = self.serverIds.join()
            }
          })
        }
      })
    },
    preview () {
      let self = this
      let wx = this.$wechat
      wx.previewImage({
        current: self.ticketUrl, // http
        urls: [self.ticketUrl] // http
      })
    },
    submit () {
      let self = this
      let $http = this.$http
      let $router = this.$router
      let fields = [
        'amount',
        'serverId'
      ]
      let data = _.merge({}, _.pick(self.$data, fields))
      if (!data.serverId) {
        self.alert('')
        return
      }
      if (!data.amount) {
        self.alert('')
        return
      }
      $http.post('a/api/bill', data).then(res => {
        let response = res.body
        self.alert(response.status.info)
        if (response.status.index === '10000') {
          $router.push('ticketlist')
        }
      })
    },
    alert (message) {
      const alert = this.$vux.alert
      alert.show({
        title: '',
        content: message
      })
    }
  },
  data () {
    return {
      title: '',
      localImages: [],
      amount: '',
      localIds: [],
      serverIds: [],
      serverId: '',
      count: 0
    }
  },
  beforeRouteEnter (to, from, next) {
    if (from.name === 'TicketList' && to.name === 'Ticket') {
      next('/me')
    } else {
      next()
    }
  }
}



